import React, { useState ,useRef,useEffect,useMemo} from 'react'
import { inject, observer } from "mobx-react"
import './history.less'
import DeriNumberFormat from "../../../../utils/DeriNumberFormat"
import dateFormat from 'date-format'
import classNames from "classnames"
import Icon from "../../../../components/Icon/Icon"
import { camelize } from "../../../../utils/utils"
import FixedColumn from "../FixedColumn/FixedColumn"

function History({ lang, trading, wallet }) {
  const [bodyWidth, setBodyWidth] = useState(0)
  const headerRef = useRef();
  const colums = useMemo(() => (
    [
      {
        label: lang['time'], key: 'time', format: (value, h) => {
          return (
            <div>
              {dateFormat.asString('yyyy-MM-dd hh:mm:ss', new Date(parseInt(value)))}&nbsp;&nbsp;
              <a target='_blank' rel='noreferrer' href={`${wallet.blockExploreUrl}/tx/${h.transactionHash}`}>
                <Icon token='scan' width='14' />
              </a>
            </div>
          )
        }
      },
      { label: lang['direction'], key: 'direction', format: value => value === 'LIQUIDATION' ? lang['liquidation'] : camelize(value) },
      { label: lang['symbol'], key: 'displaySymbol' ,format : (value,h) => value || h['symbol'] },
      {
        label: lang['entry-price'], key: 'price', format: (value, h) => {
          return (
            <DeriNumberFormat value={value} decimalScale={trading.symbolInfo.getDecimalBySymbol && trading.symbolInfo.getDecimalBySymbol(h.symbol)} />
          )
        }
      },
      { label: lang['index-price'], key: 'indexPrice', type: 'number', decimal: 2 },
      { label: lang['volume'], key: 'volume', type: 'number' },
      { label: lang['notional'], key: 'notional', type: 'number', decimal: 2 },
      { label: lang['transaction-fee'], key: 'transactionFee', type: 'number', decimal: 2 }
    ]
  ), [trading.symbolInfo]) 

  useEffect(() => {
    const resizeHandler = () => {
      if(headerRef.current){
        if(headerRef.current.scrollWidth === headerRef.current.clientWidth) {
          setBodyWidth(headerRef.current.parentElement.scrollWidth);
        } else {
          setBodyWidth(headerRef.current.scrollWidth)
        }
      }
    }
    setBodyWidth(headerRef.current.parentElement.scrollWidth)
    window.addEventListener('resize',resizeHandler)
    return () => window.removeEventListener('resize',resizeHandler)
  }, [trading.history])

  return (
    <div className='his-container'>      
      <div className='m-history'>
        <div className='h-header' ref={headerRef} style={{width : `${bodyWidth}px`}}>
          {colums.map((col, index) => (
            <div key={index} className='h-col'>{col.label}</div>
          ))}
        </div>
        <div className='h-body' style={{width : `${bodyWidth}px`}}>
          <HistoryList history={trading.history} colums={colums} lang={lang} />
        </div>
      </div>
      { trading.loaded >= 4 && <FixedColumn data={trading.history} selector='.m-history' />}
    </div>
  )
}

const HistoryList = React.memo(({history, colums, lang}) => {
  return (
    history ?
      history.length > 0
        ?
        history.map((h,i) => (
          <div className='h-row' key={i}>
            {colums.map((col, index) => {
              const value = col.format ? col.format(h[col.key], h) : h[col.key]
              const clazz = classNames('h-col', [col.key])
              return <div className={clazz} key={index}>{col.type === 'number' ? value === '-' ? '--' : <DeriNumberFormat value={value} decimalScale={col.decimal} /> : value}</div>
            })}
          </div>
        ))
        :
        <div className='no-data'><Icon token='no-data' width='48' height='48' />{lang['no-data']}</div>
      :
      <>
        <div className='h-row loading-line'></div><div className='h-row loading-line'></div><div className='h-row loading-line'></div><div className='h-row loading-line'></div><div className='h-row loading-line'></div>
      </>
  )
})

// function HistoryList({ loaded, history, colums, lang }) {
  
  
// }
export default inject('trading', 'wallet')(observer(History))