import './netWorkHint.less'
import hint from '../../assets/img/hint.svg'
import { DeriEnv } from '../../lib/web3js'
import useConfig from '../../hooks/useConfig'
import { eqInNumber, equalIgnoreCase, getNetworkList } from '../../utils/utils'
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet'
import pnc from '../../page-network-config.json'
import { useLocation } from 'react-router'
import { NETWORK_MAP } from '../../utils/Constants'

export default function NetWorkHint({ wallet }) {
  const config = useConfig();
  const walletContext = useWallet()
  const location = useLocation();
  const [isShow, setIsShow] = useState(false);
  const [networkList, setNetworkList] = useState([])
  const changeNetWork = (value) => {
    const chainId = findChainIdByNetworkName(value)
    wallet.switchNetwork(config[chainId])
  }

  const findChainIdByNetworkName = useCallback(name => {
    const chainIds = Object.keys(config);
    const pos = chainIds.findIndex(chainId => equalIgnoreCase(config[chainId].name,name))
    if(pos < 0 ){
      console.warn('can not find chain config with : ',name)
    }
    return chainIds[pos];
  })

  const getCurPageSupportedChains = useCallback(() => {
    const chainList = pnc[DeriEnv.get()];
    const pathname = location.pathname;
    const page = Object.keys(chainList).find(key => pathname.match(new RegExp(`^${key}`)))
    const supportedChains = chainList[page]
    return supportedChains
  },[location])


  const isWrongNetwork = useCallback(() => {
    const supportedChains = getCurPageSupportedChains();
    if(walletContext.isConnected()) {
      return supportedChains && !supportedChains.includes(walletContext.chainId)
    }
    if(walletContext.status === 'connecting' || walletContext.status === 'error') {
      return false
    }
    return false
  },[walletContext])

  useEffect(() => {
    if(isWrongNetwork()) {
      setIsShow(true)
    } else {
      setIsShow(false)
    }
  }, [walletContext]);

  useEffect(() => {    
    const networkList = getNetworkList(DeriEnv.get(),getCurPageSupportedChains())    
    setNetworkList(networkList)
  }, [location])

  return (
    isShow ? <div className='network-hint-box'>
      <div className='hint-img'>
        <img alt='' src={hint} />
        <div className='hint-text'>
          Error：Wrong network. Please switch to 
          {networkList.map(chain => (
            <span key={chain.name} className='network-name' onClick={() => { changeNetWork(chain.name) }}> {NETWORK_MAP[chain.name] || chain.name} </span> 
          ))}
          </div>
      </div>
    </div> : null
  )
}